import React from "react";
import Table, { TablePagination,TableBody, TableCell, TableHead, TableRow, TableFooter } from 'material-ui/Table';
import IconButton  from 'material-ui/IconButton';
import DeleteIcon from 'material-ui-icons/Delete';
import ModeEditIcon from 'material-ui-icons/ModeEdit';



export default class ProdutoLista extends React.Component {

    setPagina(numero) {
        this.props.mudaPagina(numero);
    }

    botoesPagina() {
        let botoes = [<button>&lt;&lt;</button>, <button>&lt;</button>];
        for (let x = 0; x < this.props.pagina.totalPages; x++) {
            let botao = <button 
                onClick={(evento) => {
                                this.setPagina(x);
            }}
                disabled={x == this.props.pagina.number}>{x + 1}</button>;
                botoes.push(botao);
        }
        return botoes;
    }

    botoesProduto(produto) {
        let botoes = [];
        if (this.props.editar) {
            let botao = <IconButton   fab color="primary"  onClick={(evento) => {
                                this.props.editar(produto);
            }}><ModeEditIcon /></IconButton  >;
                botoes.push(botao);
        }

        if (this.props.apagar) {
            let botao = <IconButton  fab color="primary"   onClick={(evento) => {
                                this.props.apagar(produto);
            }}>
            <DeleteIcon/></IconButton>;
                botoes.push(botao);
        }
        return botoes;
    }

    render() {

        if (!this.props.pagina.content) {
            return <div>Vazio!</div>;
        } else {

            return     <Table style={{width:"50%"}}>
        <TableHead>
          <TableRow>
            <TableCell>Nome</TableCell>
            <TableCell numeric>Valor</TableCell>
          <TableCell>Função</TableCell>
          </TableRow>
        </TableHead>           
      
      
      
    <TableBody>
        {this.props.pagina.content.map((produto) => {
                                return <TableRow key={produto.id}>
                            <TableCell >{produto.nome}</TableCell >
                            <TableCell >{produto.valor}</TableCell >
                            <TableCell >
                                {this.botoesProduto(produto)}</TableCell >
                        </TableRow>;
        })}        
    </TableBody>
    <TableFooter>
         <TablePagination
                  count={this.props.pagina.totalPages*5}
                  rowsPerPage={5}
                  rowsPerPageOptions={[5]}
                  page={this.props.pagina.number}
                  onChangePage={(event, page) => {
    this.setPagina( page );
  }}
                  
                />
        
    </TableFooter>
</Table>;
        }
    }
}


