import React from "react";
import TextField from 'material-ui/TextField';
import Button from 'material-ui/Button';
import Dialog, {
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from 'material-ui/Dialog';

export default class ProdutoItem extends React.Component {
        
        constructor(props){
            super(props);
            this.state={
                produto:{
                    nome:"",
                    valor:""
                }}
            
        }
        
        componentWillReceiveProps(props){
            
            this.setState({
                produto:props.produto
            });
        }
        
        setNome(valor){
            this.setState(
                    (anterior)=>
                            {
                            anterior.produto.nome=valor;
                            return anterior;
                            }
                    );
            
        }
        
        setValor(valor) {
            this.setState(
                    (anterior) =>{
                        anterior.produto.valor=valor;
                        return valor;
                    }
                    
                    );
            
        }
        
        confirmar(){
            if(this.state.produto.nome&&
                  this.state.produto.valor){
                    if(this.state.produto.id)
                        this.props.atualizar(this.state.produto);
                    else this.props.inserir(this.state.produto);
                    } else {
                        alert("Preencha todos os campos!");
                    }
                        
            
            
        }
        
        
        render(){
            return <Dialog open={this.props.abrir} > 




<DialogTitle>{this.state.produto.id?"Editar":"Cadastrar"}</DialogTitle>
          <DialogContent>
           
            <form>
           <TextField
                label="Nome"
                placeholder="nome..."
                value={this.state.produto.nome}
                onChange={(evento)=>this.setNome(evento.target.value)}  /><br/><br/>
      
            <TextField label="Valor" type="number" 
                value={this.state.produto.valor}
                onChange={(evento)=>this.setValor(evento.target.value)}
            />
            </form>
          </DialogContent>
          <DialogActions>
            <Button onClick={()=>this.props.cancelar()} color="primary">
              Cancelar
            </Button>
            <Button onClick={(evento)=>{this.confirmar()}} color="primary">
              Confirmar
            </Button>
          </DialogActions>

            
            </Dialog>
            
            
            
        }
} 


